header {
  color: #74717a;
  z-index: 3;
  background-color: #fff;
  justify-content: space-between;
  width: 100%;
  padding: 20px 24px 16px;
  display: flex;
  position: sticky;
  top: 0;
  left: 0;
}

.header__left {
  align-items: center;
  gap: 8px;
  display: flex;
}

.header__logo {
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  display: flex;
}

.header__menu {
  gap: 16px;
  display: flex;
}

.header__menu > li, .header__right > a {
  cursor: pointer;
}

.header__menu > li:hover, .header__right > a:hover {
  color: #171321;
}

.header__menu > li {
  display: flex;
  position: relative;
}

.header__menu > li > :is(a, .title) {
  padding: 8px;
}

.header__menu > li .menu__arrow-down {
  display: none;
  position: absolute;
  bottom: -4px;
  left: calc(50% - 8px);
}

.header__menu > li:hover .menu__arrow-down {
  display: block;
}

.header__menu .menu__filler {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 100%;
  left: 0;
}

.header__menu .sub-menu {
  color: #171321;
  visibility: hidden;
  opacity: 0;
  cursor: default;
  background-color: #fff;
  border: 1px solid #d1d0d3;
  border-radius: 4px;
  width: max-content;
  transition: all .2s;
  display: flex;
  position: absolute;
  top: 52px;
}

:is(.menu__solutions, .menu__resources, .menu__company, .menu__contact-us):hover .sub-menu {
  visibility: visible;
  opacity: 1;
}

.menu__resources .sub-menu {
  left: -120px;
}

.menu__company .sub-menu {
  left: -66px;
}

.menu__contact-us .sub-menu {
  left: -60px;
}

.header__menu .sub-menu :is(.title, .text) {
  font-weight: 500;
}

.header__menu .sub-menu .left {
  padding: 32px 24px;
}

:is(.menu__company, .menu__contact-us) .sub-menu .left {
  flex-direction: column;
  gap: 12px;
  margin-right: 58px;
  display: flex;
}

:is(.menu__company, .menu__contact-us) .sub-menu .left li:hover {
  color: #7759c2;
}

.menu__solutions .sub-menu .left__inner {
  row-gap: 32px;
  display: grid;
}

.menu__resources .sub-menu .left__inner {
  display: flex;
}

.header__menu .sub-menu .more {
  font-size: 18px;
}

.header__menu .sub-menu .left .more {
  margin-top: 22px;
}

.header__menu .sub-menu .left__inner ul {
  flex-direction: column;
  gap: 16px;
  margin-right: 58px;
  display: flex;
}

.header__menu .sub-menu .left__inner li:hover a, .header__menu .sub-menu .left__inner .title {
  color: #7759c2;
}

.header__menu .sub-menu .our-solutions {
  grid-row: 1 / 3;
}

.header__menu .sub-menu .by-company-size {
  grid-area: 1 / 2 / 2 / 3;
}

.header__menu .sub-menu .right {
